import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent implements OnInit {
  public name: string;
  public show = true;

  constructor(
    private router: Router, 
  ) { }

  ngOnInit() {
    if (
      window.location.href.match(/\/login/) !== null || 
      window.location.href === 'http://localhost:4200/' ||
      window.location.href === 'https://territorios-telefone-dev.netlify.app/'
    ) {
      this.show = false;
    } 
    
    this.name = window.localStorage['myData'] ? 
      JSON.parse(window.localStorage['myData']).name :
      '';
  }

  ionViewWillEnter() {
    this.ngOnInit();
  }

  setShow() {
    this.show = true;
  }

  homePage() {
    this.router.navigate(['/ruas']);
  }

  logout() {
    window.localStorage.clear();
    this.router.navigate(['/login']);
  }
}
