import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from "../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  public API_URL = environment.API_URL;

    constructor( 
      private http: HttpClient,
    ) { }

    public async getMain() {
      return this.http.get(this.API_URL).toPromise();
    }

    public async getAllCongregation() {
      return await this.http.get(`${this.API_URL}/congregacoes`).toPromise();
    }

    public async getCongregationByName(congregation) {
      return await this.http.get(`${this.API_URL}/congregacoes/${congregation}`).toPromise();
    }

    public async postLogin(user) {
      return await this.http.post(`${this.API_URL}/login`, user).toPromise();
    }

    public async getOneUserByUsername(username) {
      return await this.http.get(`${this.API_URL}/users/${username}`).toPromise();
    }

    public async getOneUserByName(name) {
      return await this.http.get(`${this.API_URL}/users/name/${name}`).toPromise();
    }

    public async getAllUsersByCongregation(congregation) {
      return await this.http.get(`${this.API_URL}/users/congregation/${congregation}`).toPromise();
    }

    public async postUser(user) {
      return await this.http.post(`${this.API_URL}/users`, user).toPromise();
    }

    public async updateUser(username, dataToUpdate) {
      return await this.http.put(`${this.API_URL}/users/${username}`, dataToUpdate).toPromise();
    }

    public async deleteUser(username) {
      return await this.http.delete(`${this.API_URL}/users/${username}`).toPromise();
    }

    public async getAllPhonesByAddress(address) {
      return await this.http.get(`${this.API_URL}/telefones/${address}`).toPromise();
    }

    public async getObservation(phone) {
      return await this.http.get(`${this.API_URL}/observacoes/${phone}`).toPromise();
    }

    public async putObservation(phone, dataToUpdate) {
      return await this.http.put(`${this.API_URL}/observacoes/${phone}`, dataToUpdate).toPromise();
    }

    public async deleteObservation(phone, id) {
      return await this.http.delete(`${this.API_URL}/observacoes/${phone}?id=${id}`).toPromise();
    }
}
