import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { HttpService } from '../service/http.service';

@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.page.html',
  styleUrls: ['./login-modal.page.scss'],
})
export class LoginModalPage implements OnInit {
  public formSignin: FormGroup;
  public congregations: any;

  constructor( 
    private modalController: ModalController,
    private http: HttpService,
    private alertController: AlertController,
    private formBuilder: FormBuilder
  ) { 
      this.formSignin = this.formBuilder.group({
        'name': [null, Validators.compose([Validators.required])],
        'username': [null, Validators.compose([Validators.required])],
        'password': [null, Validators.compose([Validators.required, Validators.minLength(4)])],
        'passwordCheck': [null, Validators.compose([Validators.required, Validators.minLength(4)])],
        'congregation': [null, Validators.compose([Validators.required])],
      }, {validator: this.checkPasswords});
    }

  async ngOnInit() {
    this.congregations = await this.http.getAllCongregation();
  }

  checkPasswords(group: FormGroup) { 
  let pass = group.get('password').value;
  let confirmPass = group.get('passwordCheck').value;

  return pass === confirmPass ? null : { notSame: true }     
}

  async signin() {
    const user = {
      name: this.formSignin.get('name').value,
      username: this.formSignin.get('username').value,
      password: this.formSignin.get('password').value,
      congregation: this.formSignin.get('congregation').value
    }
    try {
      await this.http.postUser(user);
      const alert = await this.alertController.create({
        header: 'Parabéns!',
        message: 'Sua conta foi criada com sucesso!',
        buttons: [
          {
            text: 'OK',
            handler: () => {
              this.dismiss();
            }
          }
        ]
      });

      await alert.present();
    } catch (error) {
      const alert = await this.alertController.create({
        header: 'Desculpe',
        message: 'Houve um erro no cadastramento, tente novamente',
        buttons: [
          {
            text: 'OK',
            handler: () => {
              this.signin();
            }
          }
        ]
      });

      await alert.present();
    }
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }
}
